    <div class="part-title">
      <h3>{{msg}}</h3>
      <p>星球数据为企业数字化保驾护航</p>
    </div>
<ul>
  <li>1、ERP系统（库存、采购、财务、会计、审批流程）。</li>
  <li>2、人工智能技术应用、移动互联应用技术。</li>
  <li>3、RFID系统（药监码、固定资产、智能仓储、防伪防窜）。</li>
  <li>4、智能产品开发与应用、工业机器人技术。</li>
  <li>5、大数据技术，在线考试系统，毕业设计，裁判文书等。</li>
</ul>
