  <div id="homePage-3" v-if=" this.$i18n.locale == 'en'">
    <div class="part-title">
      <h3>Freelancer</h3>
      <p>Custom Software Development</p>
    </div>
    <div class="carousel">
    <div @click="prevSlide" class="carousel-prev-icon-left"></div>
    <div class="carousel-slides">
      <img
        v-for="(image, index) in images"
        :key="index"
        :src="image"
        :style="{left: index * 100 + '%', 'transform': dynamicstyle}"
        alt="暂无图片"
      />
    </div>
    <div @click="nextSlide" class="carousel-prev-icon-right"></div>
  </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'PageThree',
  props: {
    speed: Number,
    direction: String
  },
  data () {
    return {
      msg: '定制开发',
      images: [
        require("../../../assets/img/freelancer/photo_2024-03-22_19-38-50.jpg"),
        require("../../../assets/img/freelancer/photo_2024-04-02_15-32-30.jpg"),
        require("../../../assets/img/freelancer/photo_2024-03-21_18-07-43.jpg"),
        require("../../../assets/img/freelancer/photo_2024-04-07_18-27-03.jpg"),
        require("../../../assets/img/freelancer/photo_2024-03-09_01-10-28.jpg"),
        require("../../../assets/img/freelancer/photo_2024-03-15_21-05-36.jpg"),
        require("../../../assets/img/freelancer/photo_2024-04-12_22-33-35.jpg"),
      ],
      dynamicstyle: "", //动态样式
      currentSlide: 0, //播放序号
      interval: Object,
    }
  },
  mounted() {
    this.startSlideshow()
  },
  methods: {
    nextSlide() {
      // 每次指针加一
      this.currentSlide = (this.currentSlide + 1) % this.images.length
      this.setStyle();
    },
    prevSlide() {
      // 每次减一，为负数时循环
      this.currentSlide = (this.currentSlide - 1 + this.images.length) % this.images.length;
      this.setStyle();
    },
    // 图片动画
    setStyle() {
      this.dynamicstyle = `translatex(-${this.currentSlide*100}%)`
    },
    // 定时器
    startSlideshow() {
      this.interval = setInterval(() => {
        this.currentSlide = (this.currentSlide + 1) % this.images.length;
        this.setStyle();
      }, 3000)
    },
    stopSlideshow() {
      clearInterval(this.interval)
    }
  }
}
</script>
